a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:after, blockquote:before, q:after, q:before {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box; }

svg {
  width: 100% !important;
  height: auto !important; }

a[href^="tel"] {
  color: inherit;
  text-decoration: none; }

button {
  border: none;
  outline: none;
  padding: 0;
  background: transparent; }

html, body {
  height: 100%; }

body {
  background: #fff;
  font-family: 'Gilroy-Light';
  font-size: 62.5%;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  color: #000; }

input, select, button {
  -webkit-appearance: none; }

img {
  max-width: 100%;
  width: auto;
  height: auto;
  vertical-align: middle; }

.video {
  position: relative;
  padding-bottom: 56.25%; }

.video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

h1 a, h2 a, h3 a, h4 a {
  color: inherit;
  text-decoration: none;
  border: none !important; }

a {
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out; }

.clearfix:before, .clearfix:after {
  content: "";
  display: table; }

.clearfix:after {
  clear: both; }

.clearfix {
  *zoom: 1; }

body {
  background: #000;
  padding: 10px;
  font-family: 'Oswald', sans-serif;
  font-weight: 300;
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  color: #666666;
  margin: 0; }

.site-wrapper {
  position: relative;
  overflow: hidden;
  left: 0;
  background-color: #fff; }

/* #Utility #Classes
============================================= */
/* Backgrounds */
.bg--cover {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.bg--white {
  background-color: #fff; }

/* Floats */
.f--left {
  float: left; }

.f--right {
  float: right; }

.link--inherit {
  color: inherit;
  text-decoration: none; }

.link--block {
  width: 100%;
  height: 100%;
  display: block;
  text-decoration: none;
  color: inherit; }

/* #Typography
============================================ */
h1, h2, h3, h4, h5, h6 {
  text-transform: uppercase;
  font-weight: 800;
  color: #000000;
  position: relative;
  margin-bottom: 20px;
  line-height: 1; }

h1 {
  font-size: 3em; }

h2 {
  font-size: 2em; }

h3 {
  font-size: 1.8em; }

h4 {
  font-size: 1.8em; }

p {
  font-size: 1.8em;
  margin-bottom: 20px; }

p:last-child {
  margin-bottom: 0; }

.underline {
  text-decoration: underline; }

strong {
  font-weight: bold; }

em, cite {
  font-style: italic; }

ul li, ol li {
  font-size: 1.6em; }

ul {
  list-style-type: disc;
  list-style-position: inside;
  font-size: 1em; }

ol {
  list-style-type: decimal;
  list-style-position: inside;
  font-size: 1em; }

ul ul, ol ul {
  list-style-type: circle;
  list-style-position: inside;
  margin-left: 20px;
  font-size: 1em; }

ol ol, ul ol {
  list-style-type: lower-latin;
  list-style-position: inside;
  margin-left: 15px;
  font-size: 1em; }

blockquote {
  border-left: 3px solid #000000;
  margin: 0;
  padding: 30px; }

blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0;
  margin-right: 0; }

blockquote p {
  display: inline; }

/* Typography - Media Queries */
@media all and (max-width: 1250px) {
  h1 {
    font-size: 3em; } }

@media all and (max-width: 767px) {
  h1 {
    font-size: 2.5em; } }

@media all and (max-width: 639px) {
  h4 {
    font-size: 1.8em; }
  p {
    font-size: 1.6em; } }

/* #Sections
============================================ */
.section {
  position: relative;
  padding: 100px 0; }

.container {
  width: 100%;
  max-width: 1600px;
  padding: 0 30px;
  margin: 0 auto; }

.align--left {
  text-align: left; }

.align--right {
  text-align: right; }

.align--center {
  text-align: center; }

/* Section - Media Queries */
@media all and (max-width: 1023px) {
  .section {
    padding: 60px 0; } }

@media all and (max-width: 767px) {
  .section {
    padding: 40px 0; } }

@media all and (max-width: 639px) {
  .container {
    padding: 0 20px; }
  .section {
    padding: 20px 0; } }

/* #Utilities */
.no-padding {
  padding: 0; }

.no-top-padding {
  padding-top: 0; }

.no-bottom-padding {
  padding-bottom: 0; }

/* #Grid
============================================ */
.flex-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  height: 100%;
  margin: 0 -20px; }
  .flex-wrapper [class^="col--"], .flex-wrapper [class*=" col--"] {
    float: none;
    padding: 0 20px; }

.col-wrapper {
  margin: 0 -20px; }
  .col-wrapper [class^="col--"], .col-wrapper [class*=" col--"] {
    float: left;
    min-height: 1px;
    padding: 0 20px; }

[class^="col--"], [class*=" col--"] {
  float: left;
  min-height: 1px;
  padding: 0 20px; }

[class^="col--"] .image, [class*=" col--"] .image {
  margin-bottom: 20px; }

.col--10 {
  width: 10%; }

.col--20 {
  width: 20%; }

.col--25 {
  width: 25%; }

.col--30 {
  width: 30%; }

.col--33 {
  width: 33.333%; }

.col--40 {
  width: 40%; }

.col--50 {
  width: 50%; }

.col--60 {
  width: 60%; }

.col--66 {
  width: 66.666%; }

.col--70 {
  width: 70%; }

.col--75 {
  width: 75%; }

.col--80 {
  width: 80%; }

.col--90 {
  width: 90%; }

.col--100 {
  width: 100%;
  float: none;
  clear: both; }

.col--no-pad {
  padding-left: 0 !important;
  padding-right: 0 !important; }

/* Clear columns */
.col--20:nth-child(5n+1), .col--25:nth-child(4n+1), .col--33:nth-child(3n+1), .col--50:nth-child(2n+1) {
  clear: left; }

/* Grid - Media Queries */
@media all and (max-width: 1023px) {
  .col-wrapper {
    margin: 0 -15px; }
  [class^="col--"], [class*=" col--"] {
    padding: 0 15px; }
  .col--20 {
    width: 33.333%; }
  .col--25 {
    width: 25%; }
  .col--33 {
    width: 33.333%; } }

@media all and (max-width: 767px) {
  .col--20, .col--25 {
    width: 50%; }
  .col--20:nth-child(5n+1), .col--25:nth-child(4n+1) {
    clear: none; }
  .col--20:nth-child(2n+1), .col--25:nth-child(2n+1) {
    clear: left; } }

@media all and (max-width: 639px) {
  .col-wrapper {
    margin: 0 -5px; }
  [class^="col--"], [class*=" col--"] {
    padding: 0 5px; }
  .col--10, .col--20, .col--25, .col--30,
  .col--33, .col--40, .col--50, .col--60,
  .col--66, .col--70, .col--75, .col--80,
  .col--90, .col--100 {
    width: 100%; } }

/* #Nav Header
============================================ */
.header {
  padding: 20px 0; }
  .header .flex-wrapper {
    justify-content: space-between;
    margin: 0; }
  .header .logo h1 {
    margin-bottom: 5px; }
  .header .logo h4 {
    font-weight: 300;
    font-size: 1.67em;
    margin-bottom: 0; }
  .header .nav ul {
    list-style: none; }
    .header .nav ul li {
      display: inline-block;
      color: #000;
      padding: 0 20px; }

@media all and (max-width: 767px) {
  .header .logo h4 {
    font-size: 1.4em; } }

/* #Cusom Sections
============================================ */
.section--intro .text h1 {
  margin-bottom: 4px; }

/* #hero
============================================ */
.section--hero .image {
  position: relative;
  margin-bottom: 0;
  overflow: hidden;
  transition: 0.3s ease-in-out; }
  .section--hero .image:before {
    content: '';
    position: relative;
    display: block;
    width: 100%;
    padding-top: calc(750 / 1200 * 100%); }

/* #featured tiles
============================================ */
.section--portfolio .portfolio-item {
  position: relative;
  margin-bottom: 40px;
  float: left; }
  .section--portfolio .portfolio-item:not(:first-child) {
    width: 50%;
    clear: none; }

.section--portfolio .image-wrapper {
  position: relative;
  overflow: hidden; }

.section--portfolio .image {
  position: relative;
  margin-bottom: 0;
  overflow: hidden;
  transition: 0.3s ease-in-out; }
  .section--portfolio .image:before {
    content: '';
    position: relative;
    display: block;
    width: 100%;
    padding-top: calc(750 / 1200 * 100%); }
  .section--portfolio .image:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in-out; }

.section--portfolio .logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 300px;
  width: 100%;
  padding: 20px;
  z-index: 100; }
  .section--portfolio .logo svg path {
    fill: #fff; }

.section--portfolio a:hover .image {
  transform: scale(1.05); }
  .section--portfolio a:hover .image:after {
    background-color: rgba(0, 0, 0, 0.7); }

@media all and (max-width: 1023px) {
  .section--portfolio .portfolio-item {
    margin-bottom: 30px; } }

@media all and (max-width: 767px) {
  .section--portfolio .col--50,
  .section--portfolio .col--33 {
    width: 100%; } }

@media all and (max-width: 639px) {
  .section--portfolio .portfolio-item {
    margin-bottom: 10px; }
  .section--portfolio .logo {
    max-width: 190px; } }

/* #section CTA
============================================ */
.section--cta .container {
  max-width: 100%;
  padding: 0; }

.section--cta .col-wrapper {
  margin: 0; }

.section--cta [class^="col--"],
.section--cta [class*=" col--"] {
  padding: 0; }
  .section--cta [class^="col--"] .col-inner,
  .section--cta [class*=" col--"] .col-inner {
    position: relative;
    min-height: 400px;
    background-color: #ABB6B7; }
  .section--cta [class^="col--"]:first-child .col-inner,
  .section--cta [class*=" col--"]:first-child .col-inner {
    background-color: #95A4A6; }

.section--cta .text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

@media all and (max-width: 639px) {
  .section--cta .container {
    max-width: 100%;
    padding: 0; }
  .section--cta [class^="col--"] .col-inner,
  .section--cta [class*=" col--"] .col-inner {
    min-height: 200px; } }

/* #Footer
============================================ */
.footer {
  position: relative;
  padding: 50px 0; }

.footer p {
  font-size: 1.2em; }
